.section {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.section_type_raid {
    height: calc(100vh - var(--header-height));
    position: relative;
    align-items: center;
    overflow: hidden;
    transition: 1s;
}

.section_type_mythic-plus {
    gap: 30px;
    padding: 30px 60px;
    position: relative;
    overflow: hidden;
    justify-content: center;
    flex-direction: column;
    transition: 1s;
}

@media all and (max-width: 1600px) {
    .section_type_raid {
        height: auto;
        padding: 30px 0;
    }
}

@media all and (max-width: 700px) {
    .section_type_raid {
        padding: 15px 0;
        gap: 15px;
    }

    .section_type_mythic-plus {
        gap: 15px;
        padding: 15px 0;
        align-items: center;
    }
}

@media all and (max-width: 645px) {
    .section_type_raid {
        flex-direction: column;
    }
}