.header {
    z-index: 3;
    width: 100%;
    max-width: 1920px;
    min-height: var(--header-height);
    display: flex;
    background-color: var(--main-color);
    position: relative;
}

.header::before {
    content: "";
    position: absolute;
    top: auto;
    left: 0;
    right: 0;
    bottom: 0;
    height: 5px;
    background: rgba(21,13,9,.8);
}

.header__logo {
    width: 40px;
    height: 40px;
    margin: auto 25px;
    background-image: url('../../images/icons8-orc-48.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: .2s;
}

.header__logo:hover {
    opacity: .7;
}

.header__title-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.header__title {
    margin: 0;
    margin-bottom: 5px;
    padding: 0;
    line-height: 100%;
    font-size: var(--h1-font-size);
    color: var(--text-color);
}

.header__brief {
    margin: 0;
    padding: 0;
    line-height: 100%;
    color: var(--text-color);
    font-size: var(--p-font-size);
}

@media all and (max-width:1030px) {
    .header__logo {
        width: 30px;
        height: 30px;
        margin: auto 20px;
    }
}